import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";
import "./MyCarousel.css"
import React from 'react'
import first from "../../img/1.jpg"
import second from "../../img/2.jpg"
import third from "../../img/3.jpg"
import fourth from "../../img/4.jpg"
import fifth from "../../img/5.jpg"
import sixth from "../../img/9.jpg"


let MyPicArr = [];

for (let i = 1; i <= 9; i++) {

    let myPicureSrc = `/images/${i}.jpg`
    MyPicArr.push(myPicureSrc)
}



const MyCarousel = ({ onClick }) => {

    const settings = {
        dots: false,
        infinite: true,
        speed: 700,
        slidesToShow: 1,
        slidesToScroll: 1,
        arrows: true,
        autoplay: true,
        autoplaySpeed: 4000,
        centerPadding: "20px",
        pauseOnHover: true,
        centerMode: true,
        //fade:true,
    };

    return (
        <div id="my-carousel">
            <Slider {...settings}>
                <div className="slide-container" >
                    <img width="100%" src={first} alt="img" />
                </div>
                <div className="slide-container" >
                    <img width="100%" src={second} alt="img" />
                </div>
                <div className="slide-container" >
                    <img width="100%" src={third} alt="img" />
                 </div>
                {/* <div className="slide-container" >
                    <img width="100%" src={fourth} alt="img" />
                </div>*/}
                <div className="slide-container" >
                    <img width="100%" src={fifth} alt="img" />
                </div>
                <div className="slide-container" >
                    <img width="100%" src={sixth} alt="img" />
                </div>  

            </Slider>
        </div>
    )
}

export default MyCarousel
